import React, { Component } from 'react';
import { Input, Button, Upload, Form } from 'antd';

import 'antd/dist/antd.css';

const formItemLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 8 },
};
const formTailLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 8, offset: 4 },
};

class DropboxForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            clientName: "",
            file: null
        }

        this.handleClientNameChange = this.handleClientNameChange.bind(this);
        this.handleFileChange = this.handleFileChange.bind(this);
        this.handleFormSubmission = this.handleFormSubmission.bind(this);
    }

    handleClientNameChange(event) {
        this.setState({ clientName: event.target.value });
    }

    handleFileChange({ file, onSuccess }) {
        this.setState({ file: file }, () =>{
            console.log(this.state.file);
            setTimeout(() => {
                onSuccess("ok");
              }, 0);
        })
    }

    createClientFolder() {
        return fetch('https://api.dropboxapi.com/2/files/create_folder_v2', {
            method: 'POST',
            mode: "cors",
            headers: {
                "Authorization": `Bearer N53yk6zNCx8AAAAAAAAAAX1hzyvOR2HvK_h3ZD-6on76RGEi1spj2ZyD5gjgmRFt`,
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                "path": `/Volt Onboarding/${this.state.clientName}`
            })
        }).then(function (response) {
            return {
                "code" : 200,
                "message" : "Success",
                "response" : response
            }
        });
    }

    uploadClientFile() {
        const formData = new FormData();

		formData.append('File', this.state.file);

        return fetch("https://content.dropboxapi.com/2/files/upload", {
            method: 'POST',
            mode: 'cors',
            headers: {
                "Authorization": `Bearer N53yk6zNCx8AAAAAAAAAAX1hzyvOR2HvK_h3ZD-6on76RGEi1spj2ZyD5gjgmRFt`,
                "Content-Type": "application/octet-stream",
                "Dropbox-API-Arg": JSON.stringify({
                    "path":   `/Volt Onboarding/${this.state.clientName}/${this.state.file.name}`,
                    "mode": "add",
                    "autorename": true,
                    "mute": false,
                    "strict_conflict": false
                })
            },
            body: this.state.file
        }).then(function (data) {
            return data.json();
        }).then(function (response) {
            return {
                "code" : 200,
                "message" : "Success",
                "response" : response
            }
        })
    }

    handleFormSubmission() {
        return this.createClientFolder()
            .then(resp => {
                if (resp.code === 200) {
                    return this.uploadClientFile()
                        .then(res => {
                            console.log(res);
                        })
                }
            })
    }

    render() {
        return (<div className="dropbox_container">
        <Form {...formItemLayout}>
            <Form.Item label="Client Name:">
                <Input type="text" value={this.state.clientName} onChange={this.handleClientNameChange} />
            </Form.Item>
            <Form.Item label="Files:" >
                <Upload value={this.state.file} beforeUpload={false} customRequest={this.handleFileChange}>
                    <Button>Click to upload</Button>
                </Upload>
            </Form.Item>
            <Form.Item {...formTailLayout}>
                <Button onClick={() => this.handleFormSubmission()}>Submit</Button>
            </Form.Item>
        </Form>
        </div>)
    }
}

export default DropboxForm;